import { PacketHistoryData, PacketInfoData } from './types'

const TEST = false
let baseURL = 'https://tychegames-backend.vercel.app'

TEST
  ? (baseURL = 'http://localhost:3000')
  : (baseURL = 'https://tychegames-backend.vercel.app')

async function createPacket(
  packet_amount: string,
  packet_asset_type: string,
  packet_cover: string,
  packet_quantity: number,
  packet_text: string,
  packet_type: string,
  initial_data: string
): Promise<{
  ok: boolean
  message?: string | unknown
  packet_id?: string
  packet_amount?: string
  packet_quantity?: string
}> {
  try {
    const response = await fetch(
      baseURL + '/api/share_app/packet/create_packet',
      {
        method: 'POST',
        body: JSON.stringify({
          packet_amount: packet_amount,
          packet_asset: packet_asset_type,
          packet_cover: packet_cover,
          packet_quantity: packet_quantity,
          packet_text: packet_text,
          packet_type: packet_type,
          initial_data: initial_data,
        }),
        mode: 'cors',
      }
    )
    const json = await response.json()
    // alert('create packet: ' + JSON.stringify(json))
    // alert(JSON.stringify(json))
    if (json.ok == false) throw json.error_msg

    // console.log('createPacket function: ' + JSON.stringify(json))
    return {
      ok: true,
      packet_id: json.packet_id,
      packet_amount: json.packet_amount,
      packet_quantity: json.packet_quantity,
    }
  } catch (e) {
    console.log('createPacket function error: ' + JSON.stringify(e))
    // alert('create packet: ' + JSON.stringify(e))
    return { ok: false, message: e }
  }
}

async function claimPacket(
  packet_id: string,
  sender_id: string,
  initial_data: string
): Promise<{
  ok: boolean
  code?: number
  message?: string
  claimed_amount?: string
}> {
  try {
    const response = await fetch(
      baseURL + '/api/share_app/packet/claim_packet',
      {
        method: 'POST',
        body: JSON.stringify({
          packet_id: packet_id,
          sender_id: sender_id,
          initial_data: initial_data,
        }),
        mode: 'cors',
      }
    )
    const json = await response.json()
    // alert('claim packet: ' + JSON.stringify(json))
    // alert(JSON.stringify(json))
    if (json.ok == false)
      throw { ok: false, code: json.code, error_msg: json.error_msg }

    // console.log('createPacket function: ' + JSON.stringify(json))
    return {
      ok: true,
      claimed_amount: json.claimed_amount,
    }
  } catch (e: any) {
    console.log('claimed packet error: ' + JSON.stringify(e))
    // alert('claimed packet' + JSON.stringify(e))
    return {
      ok: false,
      code: e.code ? e.code : 0, //1 2:
      message: e.error_msg ? e.error_msg : e,
    }
  }
}
/**
 * 
 * @param packet_id
 * @param initial_data
 * @returns
 */

async function getPacketHistory(
  packet_id: string,
  initial_data: string
): Promise<{
  ok: boolean
  packet_history?: PacketHistoryData
  error_msg?: string
}> {
  try {
    const response = await fetch(
      baseURL + '/api/share_app/packet/packet_history',
      {
        method: 'POST',
        body: JSON.stringify({
          packet_id: packet_id,
          initial_data: initial_data,
        }),
        mode: 'cors',
      }
    )
    const json = await response.json()
    if (json.ok) {
      return {
        ok: true,
        packet_history: json.packet_history as PacketHistoryData,
      }
    } else {
      return { ok: false, error_msg: json.error_msg }
    }
  } catch (e) {
    return { ok: false, error_msg: e as string }
  }
}

