import WebApp from '@twa-dev/sdk'
import { Link } from 'react-router'
import Post from './Post'
import { useUser } from '../lib/Hooks'
import Loading from './Loading'
import { useEffect } from 'react'

function Home() {
  const user = useUser(WebApp.initData)

  const startapp_param = WebApp.initDataUnsafe.start_param

  useEffect(() => {
    if (!user.userIsLoading) {
      console.log(JSON.stringify(user.userData))
    }
  }, [user.userIsLoading])

  useEffect(() => {
    console.log('user error: ' + JSON.stringify(user.userError))
  }, [user.userError])

  if (user.userIsLoading) {
    return <Loading />
  }

  if (startapp_param && startapp_param.startsWith('openPacket')) {
    return <Post />
  }
  return (
    <div
      style={{
        display: 'flex',
        flexDirection: 'column',
        //   flex: 1,
        width: '100%',
        height: '100vh',
        justifyContent: 'center',
        alignItems: 'center',
        //   background: 'red',
        color: 'black',
      }}
    >
      {/* <div>Current platform:{WebApp.platform}</div> */}
      <div>Hello {user.userData?.user_data.tg_user_id}</div>
      <Link to="/postTwitter">X</Link>
      <Link to="/post">Post</Link>
      <Link to="/scan">Scan QR test </Link>
      <Link to="/recharge"></Link>
      <Link to="/createPacket"></Link>
    </div>
  )
}

export default Home
