import { Link } from 'react-router'
import {
  openQrScanner,
  closeQrScanner,
  isQrScannerOpened,
  init,
} from '@telegram-apps/sdk'
import { postEvent } from '@telegram-apps/bridge'

import { Button } from 'antd'
import WebApp from '@twa-dev/sdk'

function ScanQR() {
  init()
  async function openScaner() {
    if (openQrScanner.isAvailable()) {
      isQrScannerOpened() // false
      const promise = openQrScanner({
        text: 'Scan the QR',
        onCaptured(qr: any) {
          if (qr === 'qr-content-we-expect') {
            closeQrScanner()
          }
        },
      })
      isQrScannerOpened() // true
      await promise
      isQrScannerOpened() // true
    } else {
      alert('Scaner not ready!')
    }
  }

  async function openInlineQuery() {
    postEvent('web_app_switch_inline_query', {
      query: 'test124',
      chat_types: ['groups', 'users'],
    })
  }

  return (
    <div
      style={{
        display: 'flex',
        flexDirection: 'column',
        //   flex: 1,
        width: '100%',
        height: '100vh',
        justifyContent: 'center',
        alignItems: 'center',
        //   background: 'red',
        color: 'black',
      }}
    >
      <Link to="/">Home</Link>
      <Button
        type="primary"
        onClick={() => {
          openScaner()
        }}
      >
        Open Scaner
      </Button>
      <Button
        type="primary"
        onClick={() => {
          openInlineQuery()
        }}
      >
        
      </Button>
    </div>
  )
}

export default ScanQR
