import useSWR, { Fetcher } from 'swr'
import { baseURL } from './util'
import { PacketHistoryData, PacketInfoData } from './types'

const user_fetcher: Fetcher<
  { ok: boolean; user_data?: any; error_msg?: string } | undefined,
  { url: string; initial_data?: string | null }
> = async ({ initial_data, url }) => {
  const res = await fetch(url, {
    method: 'POST',
    body: JSON.stringify({
      initial_data: initial_data,
    }),
    mode: 'cors',
  })
  // console.log('res ok: ' + res.ok)
  if (!res.ok) {
    const json = await res.json()
    const error = new Error('An error occurred while fetching the data.')

    // console.log('error: ' + JSON.stringify(json))

    throw error
  }
  return res.json()
}

const packet_fetcher: Fetcher<
  { ok: boolean; packet_history?: PacketHistoryData },
  { url: string; initial_data?: string | null; packet_id?: number | null }
> = async ({ url, initial_data, packet_id }) => {
  if (packet_id == null || !initial_data) return { ok: false }
  const res = await fetch(url, {
    method: 'POST',
    body: JSON.stringify({
      initial_data: initial_data,
      packet_id: packet_id,
    }),
    mode: 'cors',
  })
  if (!res.ok) {
    const json = await res.json()
    const error = new Error(json.error_msg)
    throw error
  }
  return res.json()
}

function usePacketInfo(
  initial_data?: string | null,
  packet_id?: number | null
) {
  const {
    data: packetData,
    error: packetError,
    isLoading: packetIsLoading,
    isValidating: packetIsValidating,
    mutate: packetMutate,
  } = useSWR(
    [
      baseURL + '/api/share_app/packet/get_packet_info',
      initial_data,
      packet_id,
    ],
    ([url, initial_data, packet_id]) =>
      packet_fetcher({ url, initial_data, packet_id })
  )
  return {
    packetData,
    packetError,
    packetIsLoading,
    packetIsValidating,
    packetMutate,
  }
}

//////hook

function useUser(initial_data?: string | null) {
  const {
    data: userData,
    error: userError,
    isLoading: userIsLoading,
    isValidating: userIsValidating,
    mutate: userMutate,
  } = useSWR(
    [baseURL + '/api/share_app/initial_tg_user_v2', initial_data],
    ([url, initial_data]) => user_fetcher({ url, initial_data }),
    { shouldRetryOnError: false, revalidateOnFocus: false }
  )

  return {
    userData,
    userError,
    userIsLoading,
    userIsValidating,
    userMutate,
  }
}

function usePacketId() {
  const { data: packet_id, mutate: setPacketId } = useSWR('packet_id', {
    fallbackData: '',
  })
  return { packet_id, setPacketId }
}

function usePacketAmount() {
  const { data: packet_amount, mutate: setPacketAmount } = useSWR(
    'packet_amount',
    {
      fallbackData: '',
    }
  )
  return { packet_amount, setPacketAmount }
}

function useNetwork() {
  const { data: network, mutate: setNetwork } = useSWR('network', {
    fallbackData: 'SOLANA',
  })
  return { network, setNetwork }
}

function useSolanaAddress() {
  const { data: solana_address, mutate: setSolanaAddress } = useSWR(
    'solana_address',
    {
      fallbackData: '',
    }
  )
  return { solana_address, setSolanaAddress }
}

function useTonAddress() {
  const { data: ton_address, mutate: setTonAddress } = useSWR('ton_address', {
    fallbackData: '',
  })
  return { ton_address, setTonAddress }
}

export {
  useUser,
  useNetwork,
  useSolanaAddress,
  useTonAddress,
  usePacketInfo,
  usePacketId,
  usePacketAmount,
}
